<template>
  <div id="contaier">
    <ul class="talk-bar" id="gundong">
      <li v-for="(item, index) in talkList" :key="index">
        <div class="right">
          <span>{{item.question}}</span>
        </div>
        <div class="left">
          <span>{{item.answer}}</span>
        </div>
      </li>
    </ul>
    <div class="talk-form">
      <input type="text" v-model="msg" />
      <button @click="send">Enter</button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import md5 from "js-md5";
export default {
  data() {
    return {
      msg: "",
      talkList: []
    };
  },

  methods: {
    getSign(params, key) {
      //params
      let arr = Object.keys(params).sort(); //key
      let str = "";
      arr.forEach(item => {
        if (params[item] != "") {
          // sign,, 
          str += item + "=" + encodeURI(params[item]) + "&";
        }
      });
      // app_keyURLT
      str += "app_key=" + key;
      str = md5(str).toUpperCase();
      return str;
    },
    send() {
      if (this.msg) {
        let params = {
          app_id: 2123846932,
          time_stamp: parseInt(
            Date.now()
              .toString()
              .substr(0, 10)
          ),
          nonce_str: Math.random()
            .toString(36)
            .substr(2),
          session: "10000",
          question: this.msg,
          sign: ""
        };
        console.log(this.msg);
        params.sign = this.getSign(params, "ocSwiIc3hBoYeoVu");
        axios({
          url:
            "https://bird.ioliu.cn/v2?url=https://api.ai.qq.com/fcgi-bin/nlp/nlp_textchat",
          method: "post",
          data: { ...params }
        }).then(res => {
          let obj = {
            question: this.msg,
            answer: res.data.data.answer
          };
          this.talkList.push(obj);
          this.msg = "";
          this.$nextTick(() => {
            let a = document.getElementById("gundong"); // 

            a.scrollTop = a.scrollHeight; // 
          });
        });
      } else {
      }
    }
  },
  created() {
    let obj = { title: "", color: "talk" };
    this.$emit("changeNav", obj);
  }
};
</script>
<style scoped>
/* .talk-message{
  position: fixed;
  top: 1rem;
  bottom: 2rem;
  left: 0;
  right: 0;
  background: #ddd
} */
.talk-bar {
  position: fixed;
  overflow: auto;
  /* overflow: hidden; */
  top: 1rem;
  bottom: 2rem;
  width: 100%;
}
.talk-bar span {
  padding: 5px 10px;
  border: 1px solid #ff0000;
  border-radius: 5%;
  margin: 5px;
  display: inline-block;
}
.talk-bar .left {
  text-align: left;
}
.talk-bar .right {
  text-align: right;
}

.talk-form {
  position: fixed;
  bottom: 1rem;
  width: 100%;
  height: 1rem;
  background: #ccc;
  text-align: center;
  line-height: 1rem;
}
</style>